import { IconArrowNarrowDown, IconArrowNarrowUp } from '@tabler/icons'
import React, { useEffect, useState } from 'react'

import { COLOR } from '../../styles/color'
import { RingLoader } from 'react-spinners'
import { formatNumberComma } from '../../utils/format'
import styled from 'styled-components'
import useFetch from '../../hooks/useFetch'

interface SummaryProps {
  url : string,
  dataIndex: number,
  title : string,
  calculateSumOfEvent: ({data:reportData, id:number}) => number,
  diffPrevious: ({data:reportData, id:number}) => number,
}

const INITIAL_COUNT = 0

function SummaryWidget({url, dataIndex, title, calculateSumOfEvent, diffPrevious}:SummaryProps){
  const { loading, data } = useFetch(url)
  const [sumOfEventCount, setSumOfEventCount] = useState<number>(INITIAL_COUNT)
  const [diffPreviousView, setDiffPreviousView] = useState<number>(INITIAL_COUNT)
    
  useEffect(()=>{
    if(!data) return
    setSumOfEventCount(calculateSumOfEvent({data, id:dataIndex}))
  },[data])

  useEffect(()=>{
    if(!data) return
    setDiffPreviousView(diffPrevious({data, id:dataIndex}))
  },[data])

  return (
    <WidgetContainer>
      <WidgetBody>
        {loading ? (
          <RingLoader size={30} color={COLOR.MAIN_BLUE} />
        ): (
          <>
            <WidgetTitle>{title}</WidgetTitle>
            <WidgetContent>
              <Label>SUM</Label>
              <LabelText>{data && data.headers[dataIndex].label}</LabelText>
              <Sum>{formatNumberComma(sumOfEventCount)}</Sum>
              <DiffBox>
                {diffPreviousView >= 0 
                  ? (
                    <>
                      <IconArrowNarrowUp color={COLOR.MAIN_RED}/>
                      <PositivePrevDayDiff>{formatNumberComma(diffPreviousView)}</PositivePrevDayDiff>
                    </>
                  )
                  : (
                    <>
                      <IconArrowNarrowDown color={COLOR.MAIN_BLUE}/>
                      <NegativePrevDayDiff>{formatNumberComma(diffPreviousView)}</NegativePrevDayDiff>
                    </>
                  )
                }
              </DiffBox>
            </WidgetContent>
          </>
        )}
      </WidgetBody>
    </WidgetContainer>
  )
}

const WidgetContainer = styled.section`
  padding: 10px 20px;
  min-height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
`
const WidgetBody = styled.div`
  width: 100%;
`

const WidgetContent = styled.div`
  margin-top: 10px;
`

const WidgetTitle = styled.h2`
  font-size : 14px;
  font-weight : bold;
  color: ${COLOR.MAIN_BLUE};
  margin-top: 5px;
`

const Label = styled.span`
  background: #efefef;
  color: #747474;
  padding: 3px;
  font-size: 12px;
  margin-right: 5px;
`

const LabelText = styled.span`
  font-size: 12px;
  color: grey;
  font-weight: 500;
`

const Sum = styled.div`
  font-size: 28px;
  font-weight: bold;
  margin: 5px 0;
`

const DiffBox = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
`

const NegativePrevDayDiff = styled.span`
  color: ${COLOR.MAIN_BLUE};
`

const PositivePrevDayDiff = styled.span`
  color: ${COLOR.MAIN_RED};
`


export default SummaryWidget