import { ERROR } from '../constant/errorMessage'

export function getFromLocalStorage(key) {
  let storageData
  if (global.localStorage) {
    try {
      const localData = global.localStorage.getItem(key)
      if (!localData) return
      storageData = JSON.parse(localData)[key]
    } catch (e) {
      throw new Error(ERROR.FAIL_GET_STORAGE_DATA)
    }
  }
  return storageData
}

export function setFromLocalStorage(key, value) {
  if (global.localStorage) {
    global.localStorage.setItem(
      key,
      JSON.stringify({
        [key]: value,
      })
    )
  }
}
