import {
  Area,
  AreaChart,
  Bar,
  Brush,
  CartesianGrid,
  ComposedChart,
  Legend,
  Line,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';
import React, { useEffect, useState } from 'react'
import { formatDate, formatNumberComma, formatNumberUnit } from '../../utils/format';

import { COLOR } from '../../styles/color'
import { LABEL } from '../../constant/report'
import { RingLoader } from 'react-spinners'
import { WidgetProps } from 'widget';
import { getFormatDAU } from '../../utils/handleReportData';
import styled from 'styled-components'
import useFetch from '../../hooks/useFetch'

interface graphDataState {
  "Daily" : string,
  "Unique Event Count" : number,
  "Total Event Count": number,
}

function SerialWidget({url, title}:WidgetProps){
  const { loading, data } = useFetch(url)
  const [graphData, setGraphData] = useState<graphDataState[]>([])

  useEffect(()=>{
    if(!data) return
    setGraphData(getFormatDAU(data))
  },[data])
  
  const handleMouseEvent = (e) => {
    const target = e.target as HTMLElement;
    if (
      target.classList.contains('recharts-brush') ||
      target.parentElement?.classList.contains('recharts-brush') ||
      target.parentElement?.parentElement?.classList.contains('recharts-brush')
    ) {
      e.stopPropagation();
    }
  }

  return (
    <WidgetContainer onMouseDown={handleMouseEvent}>
      <WidgetBody>
        {loading ? (
          <RingLoader size={30} color={COLOR.MAIN_BLUE} />
        ): (
          <>
            <WidgetTitle>{title}</WidgetTitle>
            <ResponsiveContainer width="100%" aspect={3}>
              <ComposedChart
                width={600}
                height={400}
                data={graphData}
                margin={{ top: 80, right: 20, bottom: 0, left: 20 }}
              >
                <Brush
                  dataKey={LABEL.DAILY}
                  height={40}
                  y={0}
                  startIndex={60}
                  stroke="#8fb4cf"
                >
                  <AreaChart>
                    <Area
                      type="monotone"
                      dataKey={LABEL.UNIQUE_EVENT_COUNT}
                      name={LABEL.UNIQUE_EVENT_COUNT}
                      stroke="gainsboro"
                      fill="gainsboro"
                    />
                  </AreaChart>
                </Brush>
                <CartesianGrid vertical={true} />
                <XAxis 
                  tickFormatter={formatDate}
                  dataKey={LABEL.DAILY}
                  scale="band"
                  axisLine={false}
                  minTickGap={80}
                  style={{
                    fontSize: "12px",
                  }}
                />
                <YAxis 
                  tickFormatter={formatNumberUnit} 
                  yAxisId="left" 
                  tickCount={7}
                  style={{
                    fontSize: "12px",
                  }}
                />
                <YAxis 
                  tickFormatter={formatNumberUnit} 
                  yAxisId="right" 
                  tickCount={7}
                  orientation="right" 
                  style={{
                    fontSize: "12px",
                  }}
                />
                <Tooltip formatter={formatNumberComma}/>
                <Legend />
                <Line yAxisId="right" dataKey={LABEL.UNIQUE_EVENT_COUNT} stroke={COLOR.CHART_SERIAL_GREEN} />
                <Bar yAxisId="left" dataKey={LABEL.TOTAL_EVENT_COUNT} barSize={7} fill={COLOR.CHART_SERIAL_GREEN} />
              </ComposedChart>
            </ResponsiveContainer>
          </>
        )}
      </WidgetBody>
    </WidgetContainer>
  )
}

const WidgetContainer = styled.section`
  padding: 10px 20px;
  min-height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
`
const WidgetBody = styled.div`
  width: 100%;
`

const WidgetTitle = styled.h2`
  font-size : 13px;
  color: ${COLOR.MAIN_BLUE};
  margin-top: 5px;
`


export default SerialWidget




