import React, { useEffect, useState } from 'react'

import { ERROR } from '../constant/errorMessage'
import { reportData } from 'reportType'
import { requestJsonData } from '../api/requestData'

interface stateTypes {
  loading: boolean
  data?: reportData
}

export default function useFetch(url: string): stateTypes {
  const [data, setData] = useState<reportData>()
  const [loading, setLoading] = useState<boolean>(true)

  const fetchData = async () => {
    try {
      setLoading(true)
      await requestJsonData(url, setData)
      setLoading(false)
    } catch (e) {
      throw new Error(ERROR.FAIL_HTTP_REQUEST)
    }
  }

  useEffect(() => {
    fetchData()
  }, [url])
  
  return { loading, data }
}
