import { reportData } from 'reportType'

interface countDataType {
  [key: string]: number
}

function calculateSumOfEvent({ data, id }) {
  return data.rows
    .map((row) => row[id])
    .reduce((sum, currView) => sum + +currView, 0)
}

function diffPreviosEvent({ data, id }) {
  const sortedData = getSortedDataByDay(data)
  const lastIndex = sortedData.length - 1
  return +sortedData[lastIndex][id] - +sortedData[lastIndex - 1][id]
}

function getSortedDataByDay(data) {
  // data.rows 배열의 row[0]은 '2020-10-01'과 같은 string 날짜 데이터
  return [...data.rows].sort(
    (prev, next) => new Date(prev[0]).getTime() - new Date(next[0]).getTime()
  )
}

function getFormatDAU(data: reportData) {
  return getSortedDataByDay(data).map(([daily, uniqueView, pageView]) => ({
    Daily: daily,
    'Unique Event Count': +uniqueView,
    'Total Event Count': +pageView,
  }))
}

function countUniqueViewByReferral(data: reportData) {
  const uniqueViewsByReferral = {}
  data.rows.forEach(([refHost, uniqueView]) => {
    uniqueViewsByReferral[refHost] = uniqueViewsByReferral[refHost]
      ? uniqueViewsByReferral[refHost] + +uniqueView
      : +uniqueView
  })
  return uniqueViewsByReferral
}

function getSortedByCount(countData: countDataType) {
  return Object.entries(countData).sort((prev, next) => next[1] - prev[1])
}

function combineUnderRank(sortedData) {
  if (sortedData.length <= 4) return [...sortedData]
  const etc = sortedData.slice(4).reduce(
    (result, current) => {
      result[1] += current[1]
      return result
    },
    ['etc', 0]
  )
  return [...sortedData.slice(0, 4), etc]
}

function changeDatasetForChart(combinedData) {
  return combinedData.map(([ref_host, views]) => ({
    name: ref_host,
    value: views,
  }))
}

function transformPieData(data: reportData) {
  const countData = countUniqueViewByReferral(data)
  const sortedData = getSortedByCount(countData)
  const combinedData = combineUnderRank(sortedData)
  return changeDatasetForChart(combinedData)
}

export { calculateSumOfEvent, diffPreviosEvent, getFormatDAU, transformPieData }
