import "react-base-table/styles.css"

import React, { useEffect, useState } from "react"
import Table, { AutoResizer, unflatten } from "react-base-table"

import { COLOR } from "../../styles/color"
import { RingLoader } from "react-spinners"
import { WIDGET } from "../../constant/textMessage"
import { WidgetProps } from "widget"
import { getTreeData } from "../../utils/handleTableData"
import styled from 'styled-components'
import useFetch from "../../hooks/useFetch"

const columns = [
  {
    dataKey: WIDGET.TABLE_EXPAND_COLUMN_KEY,
    key: WIDGET.TABLE_EXPAND_COLUMN_KEY,
    title: WIDGET.TABLE_FIRST_COLUMN_TITLE,
    width: 300,
  },
  {
    dataKey: WIDGET.TABLE_SECOND_COLUMN_KEY,
    key: WIDGET.TABLE_SECOND_COLUMN_KEY,
    title: WIDGET.TABLE_SECOND_COLUMN_TITLE,
    width: 200,
  },
]

function DataTableWidget({url, title}:WidgetProps){
  const {loading, data} = useFetch(url)
  const [result, setResult] = useState<any>()

  useEffect(()=>{
    if(!data) return
    const treeData = unflatten(getTreeData(data))
    setResult(treeData)
  },[data])
  
  return (
    <WidgetContainer>
      <WidgetBody>
        {loading ? (
          <RingLoader size={30} color={COLOR.MAIN_BLUE} />
        ): (
          <>
            <WidgetTitle>{title}</WidgetTitle>
            <div style={{ width: "100%", height: "300px", overflow:"hidden"}}>
              <AutoResizer>
                {({ width, height }) => (
                  <Table
                    fixed
                    width={width}
                    height={height}
                    columns={columns}
                    data={result}
                    expandColumnKey={WIDGET.TABLE_EXPAND_COLUMN_KEY}
                    rowHeight={40}
                    scrollToTop={100}
                    scrollToLeft={80}
                    forceUpdateTable
                  />
                )}
              </AutoResizer>
            </div>
          </>
        )}
      </WidgetBody>
    </WidgetContainer>
  )
}

const WidgetContainer = styled.section`
  padding: 10px 20px;
  min-height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
`
const WidgetBody = styled.div`
  width: 100%;
`

const WidgetTitle = styled.h2`
  font-size : 13px;
  color: ${COLOR.MAIN_BLUE};
  margin-top: 5px;
`

export default DataTableWidget