import { WIDGET } from '../constant/textMessage'
import { reportData } from 'reportType'

let emptyId = 1
const expandColumnKey = WIDGET.TABLE_EXPAND_COLUMN_KEY

const sortByKey = (data, key) => {
  return [...data].sort((prev, next) => next[key] - prev[key])
}

const combineDataByGroup = (data: reportData) => {
  const combinedData = {}
  const summary = {}

  data.rows.forEach(([id1, id2, id3, uniqueView]) => {
    const country = id1 || '(empty country)'
    const region = id2 || '(empty region)'
    const city = id3 || '(empty city)'

    if (!combinedData[country]) combinedData[country] = {}
    if (!combinedData[country][region]) combinedData[country][region] = {}
    if (!combinedData[country][region][city])
      combinedData[country][region][city] = {}
    combinedData[country][region][city]['uniqueView'] = +uniqueView

    summary[country] = summary[country]
      ? summary[country] + +uniqueView
      : +uniqueView
    summary[region] = summary[region]
      ? summary[region] + +uniqueView
      : +uniqueView
    summary[city] = summary[city] ? summary[city] + +uniqueView : +uniqueView
  })
  return [combinedData, summary]
}

export function getTreeData(data: reportData) {
  const [combinedData, summary] = combineDataByGroup(data)
  const topData = Object.keys(combinedData).map((currentId) => ({
    id: currentId,
    parentId: null,
    [WIDGET.TABLE_SECOND_COLUMN_KEY]: summary[currentId],
    [expandColumnKey]: currentId,
  }))

  const secondData = [
    ...topData
      .map((parentData) => {
        return Object.keys(combinedData[parentData.id]).map((currentId) => ({
          id: currentId,
          parentId: parentData.id,
          [WIDGET.TABLE_SECOND_COLUMN_KEY]: summary[currentId],
          [expandColumnKey]: currentId,
        }))
      })
      .flat(),
  ]

  const thirdData = [
    ...secondData
      .map((parentData) => {
        return Object.keys(
          combinedData[parentData.parentId][parentData.id]
        ).map((currentId) => ({
          id:
            currentId === '(empty)' ? `(empty)-${emptyId++}` : `${currentId}-2`,
          parentId: parentData.id,
          [WIDGET.TABLE_SECOND_COLUMN_KEY]: summary[currentId],
          [expandColumnKey]: currentId,
        }))
      })
      .flat(),
  ]

  return sortByKey(
    [...topData, ...secondData, ...thirdData],
    WIDGET.TABLE_SECOND_COLUMN_KEY
  )
}
