export function formatNumberComma(num: number) {
  return num.toLocaleString()
}

export function formatNumberUnit(num: number) {
  if (num < 1000) return num + ''
  if (num < 1e6) return Math.floor(num / 1e3) + 'k'
  if (num < 1e9) return Math.floor(num / 1e6) + 'M'
  if (num < 1e9) return Math.floor(num / 1e6) + 'B'
  return Math.floor(num / 1e9) + 'T'
}

function formatNumberZero(num: number) {
  return num < 10 ? '0' + num : num + ''
}

export function formatDate(date: string) {
  const dateInstance = new Date(date)
  return `${formatNumberZero(dateInstance.getMonth() + 1)}-${formatNumberZero(
    dateInstance.getDate()
  )}-${dateInstance.getFullYear()}`
}
