import { Cell, Legend, Pie, PieChart, ResponsiveContainer, Sector } from 'recharts'
import React, { useEffect, useState } from 'react'

import { COLOR } from '../../styles/color'
import { RingLoader } from 'react-spinners'
import { WidgetProps } from 'widget'
import styled from 'styled-components'
import { transformPieData } from '../../utils/handleReportData'
import useFetch from '../../hooks/useFetch'

const renderCustomLegend = (name) => {
  return (
    <span style={{ fontSize: "11px", color: "black" }}>{name}</span>
  )
}

const renderCustomLabel = (props) => {
  const RADIAN = Math.PI / 180
  const { cx, cy, midAngle, innerRadius, outerRadius, startAngle, endAngle, fill, name } = props
  const sin = Math.sin(-RADIAN * midAngle)
  const cos = Math.cos(-RADIAN * midAngle)
  const sx = cx + (outerRadius + 10) * cos
  const sy = cy + (outerRadius + 10) * sin
  const mx = cx + (outerRadius + 30) * cos
  const my = cy + (outerRadius + 30) * sin
  const ex = mx + (cos >= 0 ? 1 : -1) * 22
  const ey = my
  const textAnchor = cos >= 0 ? 'start' : 'end'

  return (
    <g>
      <Sector
        cx={cx}
        cy={cy}
        innerRadius={innerRadius}
        outerRadius={outerRadius}
        startAngle={startAngle}
        endAngle={endAngle}
        fill={fill}
      />
      <path d={`M${sx},${sy}L${mx},${my}L${ex},${ey}`} stroke={fill} fill="none" />
      <text 
        x={ex + (cos >= 0 ? 1 : -1) * 12} 
        y={ey} 
        textAnchor={textAnchor} 
        fill="#333"
        style={{fontSize: "11px"}}
      >
        {name}
      </text>
    </g>
  )
}

function PieWidget({url, title}:WidgetProps){
  const { loading, data } = useFetch(url)
  const [chartData, setChartData] = useState([])

  useEffect(()=>{
    if(!data) return
    setChartData(transformPieData(data))
  },[data])

  return (
    <WidgetContainer>
      <WidgetBody>
        {loading ? (
          <RingLoader size={30} color={COLOR.MAIN_BLUE} />
        ): (
          <>
            <WidgetTitle>{title}</WidgetTitle>
            <ResponsiveContainer width="100%" aspect={2}>
              <PieChart width={200} height={200}>
                <Pie
                  data={chartData}
                  isAnimationActive={false}
                  labelLine={true}
                  outerRadius={80}
                  dataKey="value"
                  label={renderCustomLabel}
                  style={{
                    fontSize: '11px'
                  }}
                >
                  {chartData && chartData.map((_, index) => (
                    <Cell
                      key={`cell-${index}`}
                      fill={COLOR.CHART_PIE_COLORS[index % COLOR.CHART_PIE_COLORS.length]}
                    />
                  ))}
                </Pie>
                <Legend formatter={renderCustomLegend} />
              </PieChart>
            </ResponsiveContainer>
          </>
        )}
      </WidgetBody>
    </WidgetContainer>
  )
}

const WidgetContainer = styled.section`
  padding: 10px 20px;
  min-height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
`
const WidgetBody = styled.div`
  width: 100%;
`

const WidgetTitle = styled.h2`
  font-size : 13px;
  color: ${COLOR.MAIN_BLUE};
  margin-top: 5px;
`

export default PieWidget




