import { DASHBOARD, WIDGET } from '../constant/textMessage';
import React, { useState } from 'react'
import { Responsive, WidthProvider } from "react-grid-layout";
import { calculateSumOfEvent, diffPreviosEvent } from '../utils/handleReportData';
import { getFromLocalStorage, setFromLocalStorage } from '../utils/handleLocalStorage';

import { API } from '../constant/url';
import Button from '../components/Button'
import { COLOR } from '../styles/color';
import DataTableWidget from '../components/widgets/DataTableWidget';
import PieWidget from '../components/widgets/PieWidget';
import { REPORT } from '../constant/report';
import { STORAGE_KEYS } from '../constant/storageKey';
import SerialWidget from '../components/widgets/SerialWidget';
import SummaryWidget from '../components/widgets/SummaryWidget';
import { layoutConfig } from '../config/layoutConfig'
import styled from 'styled-components'

interface styleProps {
  boardBackground ?: string
  widgetBackground ?: string
  controlBackground ?: string
}

const ResponsiveReactGridLayout = WidthProvider(Responsive);
const savedLayouts = getFromLocalStorage(STORAGE_KEYS.LAYOUTS) || layoutConfig;

function Dashboard(){
  const [layouts, setLayouts] = useState(savedLayouts);
  const onLayoutChange = (_, newLayouts) =>{
    setLayouts(newLayouts)
  }

  const saveLayout = () => {
    setFromLocalStorage(STORAGE_KEYS.LAYOUTS, layouts)
  }
  
  const resetPrevLayout = () => {
    setLayouts(getFromLocalStorage(STORAGE_KEYS.LAYOUTS))
  }

  return(
    <>
      <H1>{DASHBOARD.TITLE}</H1>
      <DashboardContainer>
        <DashboardControlBar>
          <Button 
            bgColor={COLOR.BUTTON_WHITE}
            color={COLOR.BUTTON_GRAY}
            onClick={()=>resetPrevLayout()}
            >
            {DASHBOARD.EDIT_CANCLE}
          </Button>
          <Button
            bgColor={COLOR.MAIN_BLUE}
            color={COLOR.BUTTON_WHITE}
            onClick={()=>saveLayout()}
          >
            {DASHBOARD.EDIT_COMPLETE}
          </Button>
        </DashboardControlBar>
        <GridContainer>
          <ResponsiveReactGridLayout
            className="layout"
            cols={{ lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 }}
            rowHeight={30}
            layouts={layouts}
            onLayoutChange={(_, newLayouts) =>
              onLayoutChange(_, newLayouts)
            }
          >
            <Item key="graph1">
              <SummaryWidget 
                url={API.EVENT_1}
                dataIndex={REPORT.UNIQUE_VIEW_ID}
                title={WIDGET.UNIQUE_VIEW_TITLE} 
                calculateSumOfEvent={calculateSumOfEvent}
                diffPrevious={diffPreviosEvent}
              />
            </Item>
            <Item key="graph2">
              <SummaryWidget 
                url={API.EVENT_1}
                dataIndex={REPORT.PAGE_VIEW_ID}
                title={WIDGET.PAGE_VIEW_TITLE} 
                calculateSumOfEvent={calculateSumOfEvent}
                diffPrevious={diffPreviosEvent}
              />
            </Item>
            <Item key="graph3">
              <SerialWidget
                url={API.EVENT_1}
                title={WIDGET.DAU_TITLE}
              />
            </Item>
            <Item key="graph4">
              <PieWidget 
                url={API.EVENT_3}
                title={WIDGET.TOP_REFERRAL_TITLE}
              />
            </Item>
            <Item key="graph5">
              <DataTableWidget 
                url={API.EVENT_4}
                title={WIDGET.TOP_REFERRAL_TITLE}
              />
            </Item>
          </ResponsiveReactGridLayout>
        </GridContainer>
      </DashboardContainer>
    </>
  )
}

const H1 = styled.h1`
  font-size : 20px;
  font-style : bold;
  margin: 20px 0;
  text-align : center;
`

const DashboardContainer = styled.article``

const DashboardControlBar = styled.div<styleProps>`
  background-color: ${COLOR.DASHBOARD_CONTROL_BACKGROUND};
  height : 80px;
  display: flex;
  justify-content: end;
  align-items: center;
`
const GridContainer = styled.div<styleProps>`
  background-color: ${COLOR.BOARD_BACKGROUND};
`

const Item = styled.div<styleProps>`
  background-color: ${COLOR.WIDGET_BACKGROUND};
  filter: drop-shadow(3px 3px 3px rgba(0,0,0,0.2));
`

export default Dashboard